import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import RatePercent from "./RatePercent";
import Rating from "./Rating";
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
import Comment from "./Comment";
import CircularProgress from '@mui/material/CircularProgress';
import KeyboardArrowRightIcon from '@mui/icons-material/KeyboardArrowRight';
import axios from "axios";

// <style>
    const span = {
        verticalAlign:"middle"
    }
    const loadingSelectStyle = {width:"26px",height:"26px",verticalAlign:"middle",marginLeft:"10px"}
// </style>

function Review({ rating, productId,allRating,userId,userHelp }) {
  const [loading,setLoading] = useState(false);
  const [ratingStar,setRatingStar] = useState({
    star1:"",
    star2:"",
    star3:"",
    star4:"",
    star5:"",
  });
  const [reviews,setReviews] = useState([]);

  const fetchReview = async() =>{
    setLoading(true);
    const res = await axios.get("/get-all-review/"+productId);
    setReviews(res.data);
    setSeeMore(false);
    setLoading(false);
  }

  useEffect(()=>{
    const getReviews = async() =>{
      try {
        const res = await axios.get("/get-few-review/"+productId);
        setRatingStar(res.data.ratingStar);
        setReviews(res.data.review);

        if(!res.data.review.length){
          setSeeMore(false)
        }
      } catch (error) {
        console.log(error);
      }
    }
    getReviews();
  },[productId])

  const [seeMore,setSeeMore] = useState(true);

  const [loadingSel,setLoadingSel] = useState(false);

  return (
    <section className="Review containers bg-light mb-4">
      {/* left column */}
      <div>
        <h2 className="fw-bold">Customer reviews</h2>
        <div className="d-flex align-items-center mb-2">
          <Rating rating={rating} style={{ fontSize: "27px" }} />
          <span style={{ fontSize: "18px", marginLeft: "10px" }}>
            {parseFloat(rating.toFixed(2))} out of 5
          </span>
        </div>
        <p style={{ color: "#4c4c4c" }}>{allRating} global ratings</p>
        <div className="d-grid" style={{ gap: "10px" }}>
          <RatePercent star="5" percent={ratingStar.star5 / allRating ? parseFloat(((ratingStar.star5 / allRating) * 100 ).toFixed(2)): 0} />
          <RatePercent star="4" percent={ratingStar.star5 / allRating ? parseFloat(((ratingStar.star4 / allRating) * 100 ).toFixed(2)): 0} />
          <RatePercent star="3" percent={ratingStar.star5 / allRating ? parseFloat(((ratingStar.star3 / allRating) * 100 ).toFixed(2)): 0} />
          <RatePercent star="2" percent={ratingStar.star5 / allRating ? parseFloat(((ratingStar.star2 / allRating) * 100 ).toFixed(2)): 0} />
          <RatePercent star="1" percent={ratingStar.star5 / allRating ? parseFloat(((ratingStar.star1 / allRating) * 100 ).toFixed(2)): 0} />
        </div>
        <details className='mt-3'>
          <summary className="link d-inline-block mb-1">
              <span className='arrow'><ArrowDropDownIcon/></span>
              <span style={span}>How are ratings calculated?</span>
          </summary>
          <div>
            To calculate the overall star rating and percentage breakdown by
            star, we don’t use a simple average. Instead, our system considers
            things like how recent a review is and if the reviewer bought the
            item on Amazon. It also analyzes reviews to verify trustworthiness.
          </div>
        </details>
        <hr style={{ margin: "30px 0", opacity: ".15" }} />
        <h4 className="fw-bold">Review this product</h4>
        <p>Share your thoughts with other customers</p>
        <Link to={"/create-review/"+productId} className="btns w-100">
          Write a customer review
        </Link>
      </div>
      {/* right column */}
      <div>
        <form className='mb-4'>
          <select onChange={async(e)=>{
            setLoadingSel(true);
            try {
              if(e.target.value === "Recent Review"){
                const res = await axios.get("/get-all-review/"+productId);
                setReviews(res.data);
              }
              else if(e.target.value === "Top review"){
                const res = await axios.get("/get-all-top-review/"+productId);
                setReviews(res.data);
              }
              setLoadingSel(false);
              setSeeMore(false)
            } catch (error) {
              console.log(error);
            }
          }}>
            <option value="Recent Review">Recent Review</option>
            <option value="Top review">Top review</option>
          </select>
          {
            loadingSel ?
              <CircularProgress style={loadingSelectStyle}/> :
              ""
          }
        </form>
        <h5 className='fw-bold'>Reviews of customers</h5>
        <div className="d-grid mt-3" style={{gap:"40px"}}>
          {
            reviews.length ?
              reviews.map((elem,index)=>{
                return <Comment key={index} userId={userId} userHelp={userHelp} commentInfo={elem}/>
              }) :
              <span className="text-secondary">No reviews for this product</span>
          }
        </div>
        {/* see more */}
        {
          loading ? 
            <div className="loading p-1 mt-3">
              <CircularProgress />
            </div> :
              seeMore ?
              <div className='d-block mt-3'><span className="link" onClick={fetchReview}>See more<KeyboardArrowRightIcon fontSize="small"/></span></div>
              : ""
        }
      </div>
    </section>
  );
}

export default Review;
