import React, {useEffect, useState } from "react";
import Error from "./Error";
import axios from "axios";
import Load from "../componants/Load";
import EditLocationAltIcon from '@mui/icons-material/EditLocationAlt';
import DeleteForeverIcon from '@mui/icons-material/DeleteForever';
import Button from '@mui/material/Button';
import {Link} from 'react-router-dom';
import { useHistory } from 'react-router-dom';

const heading = {
  background: "#3189e3",
  color: "#fff",
  textAlign: "center",
  padding: "10px",
};
const iconStyle = {
    background:"#237ae7",color:"#fff",padding:"1px 2px",fontSize:"22px",cursor:"pointer"
}
const mainDiv = {
  maxWidth: "500px",
  padding: "20px",
  background: "#fff",
  margin: "0 auto",
};
const contentLi = {listStyle:"disc",marginBottom:"5px",userSelect:"none"}

function CreateProduct() {
  const [info,setInfo] = useState({
    name:"",
    category:'',
    price:"",
    available:"",
    achivement:"None",
    detail:[],
    content:[],
  });
  const [category,setCategory] = useState([]);
  useEffect(()=>{
    axios.get('/get-category')
    .then((res)=>{
      setCategory(res.data)
      console.log(res);
      setInfo({...info,category:res.data[0].name})
    }).catch((err)=>{
      console.log(err)
    })
  },[]);

  const history = useHistory();
  const [file,setFile] = useState();

  const [admin, setAdmin] = useState(false);

  axios
    .get("/get-admin")
    .then((res) => {
      const load_load = document.querySelector(".load_load");
      load_load.style.animation = "load_end 0.7s linear";
      setTimeout(() => {
        load_load.classList.add("d-none");
      }, 720);
      if (res.data === "okay") {
        setAdmin(true);
      } else {
        setAdmin(false);
      }
    })
    .catch((err) => {
      console.log(err);
    });

    const [content,setContent] = useState([]);
    const [inputContent,setInputContent] = useState('');

    const [detail,setDetail] = useState([]);

  return (
    <>
      <Load />
      {admin ? (
        <>
          <h2 style={heading}>Amazon Admin Panal - Create Product</h2>
          <div className="px-4 py-2">
              <Link to='/' className='btn btn-primary'>Visit Amazon</Link>
              <Link to='/admin/create-category' className='btn btn-primary mx-2'>Create Category</Link>
              <Link to='/admin' className='btn btn-primary'>Admin Panal</Link>
          </div>
          <div style={mainDiv} className='border'>
            <form className="form" onSubmit={(e)=>{
              e.preventDefault();
              const formData = new FormData();

              for (let i = 0; i < file.length; i++) {
                formData.append('img'+i,file[i])
              }

              const btn = document.getElementById('btn');
              btn.disabled = true;

              axios.post('/add-img-product',formData,{
                headers:{
                  "Content-Type":"multipart/form-data"
                }
              }).then((res)=>{
                console.log(res);

                axios.post('/add-product',{
                  info,
                  imgSrc:res.data
                }).then((res)=>{
                  btn.removeAttribute('disabled');
                  // history.push('/admin');
                  alert("Good job")
                }).catch((err)=>{
                  btn.removeAttribute('disabled')
                  console.log(err);
                })
              }).catch((err)=>{
                btn.removeAttribute('disabled')
                console.log(err);
              })
            }}>
              <div>
                <label htmlFor="name" className="fw-bold mb-1">
                  Product Name :
                </label>
                <input
                  placeholder="Enter product name here"
                  id="name"
                  type="text"
                  className="form-control" required
                  onChange={e => setInfo({...info,name:e.target.value})}
                />
              </div>
              <div>
                <label htmlFor="category_sel" className="fw-bold mb-1">
                  Product Category :
                </label>
                <div className="d-flex align-items-center">
                  <select onChange={(e)=>{
                    setInfo({...info, category:e.target.value})
                  }} id="category_sel" className="d-block" 
                  >
                    {
                      category.map((elem,index)=>{
                        return <option key={index} value={elem.name}>{elem.name}</option>
                      })
                    }
                  </select>
                  <Link to='/admin/create-category' className="btn btn-success btn-sm ms-2">
                    Create Category
                  </Link>
                </div>
              </div>
              <div>
                <label htmlFor="price" className="fw-bold mb-1">
                  Product Price ($) :
                </label>
                <input
                  onChange={(e)=>{
                    setInfo({...info, price:e.target.value})
                  }}
                  placeholder="Enter product price here ($)"
                  id="price"
                  type="text"
                  className="form-control" required
                />
                <small className="text-secondary">Example = <i><small>12.55</small></i>, <i><small>30</small></i></small>
              </div>
              <div>
                <label htmlFor="available" className="fw-bold mb-1">
                  Available :
                </label>
                <input
                  onChange={(e)=>{
                    setInfo({...info, available:e.target.value})
                  }}
                  placeholder="Enter how much remains"
                  id="available"
                  type="number"
                  className="form-control" required
                />
              </div>
              <div>
                <label htmlFor="achieve" className="fw-bold mb-1">
                  Achievement :
                </label>
                <select onChange={(e)=>{
                  setInfo({...info,achivement:e.target.value})
                }} id="achieve" className="d-block" required
                >
                  <option value="Computer and tech">None</option>
                  <option value="amazon's choice">Amazon's choice</option>
                  <option value="bext seller">Best seller</option>
                </select>
              </div>
              <div>
                <label htmlFor="photo" className="fw-bold mb-1">
                  Photos (Only 5 is allowed) :
                </label>
                <input
                  accept="image/*"
                  onChange={(e) => {
                    if (e.target.files.length > 5) {
                      alert("Seleting more than 5 photos is invalid");
                      e.target.value = "";
                    }else{
                      setFile(e.target.files)
                    }
                  }}
                  id="photo"
                  multiple
                  type="file"
                  className="form-control" required
                />
              </div>
              <div>
                <label className="fw-bold mb-1">
                  Detail :
                </label>
                  {
                    detail.map((val,index)=>{
                      return (
                        <div key={index} className="mb-3" style={{ padding: "0 20px" }}>
                          <div className="adminPage_detail">
                            <b>{val.property} :</b>
                            <span className="ms-2">{val.value}</span>
                            <div className="adminPage_detail_edit">
                              <span onDoubleClick={()=>{
                                const property = document.getElementById('property');
                                const value = property.nextElementSibling;

                                property.value = val.property;
                                value.value = val.value;

                                setDetail(detail.filter((elem)=>{
                                  return elem.property !== val.property && elem.value !== val.value
                                }))

                                property.focus();
                              }}>
                                <EditLocationAltIcon style={iconStyle}/>
                              </span>
                              <span onDoubleClick={(e)=>{
                                setDetail(detail.filter((elem)=>{
                                  return elem.property !== val.property && elem.value !== val.value
                                }))
                              }}>
                                <DeleteForeverIcon className='ms-2' style={iconStyle}/>
                              </span>
                            </div>
                          </div>
                        </div>
                      )
                    })
                  }
             
                <div className="form d-flex">
                  <input id='property' type="text" placeholder="Property" defaultValue=''/>
                  <input type="text" placeholder="Value" defaultValue=''/>
                  <button onClick={(e)=>{
                      const value = e.target.previousElementSibling;
                      const property = value.previousElementSibling;

                      if(property.value && value.value){
                        setDetail([...detail,{property:property.value,value:value.value}]);
                        info.detail.push({
                          property:property.value,
                          value:value.value
                        })
                        property.value = '';
                        value.value = '';
                      }

                    }} type='button' className="btns4">
                    ADD
                  </button>
                </div>
              </div>

              <div>
                <label htmlFor="achieve" className="fw-bold mb-1">
                  Content :
                </label>
                <div>
                  <ul className='mb-0'>
                    {
                      content.map((val,index)=>{
                        return <li onDoubleClick={(e)=>{
                          let index = info.content.indexOf(e.target.innerText);
                          info.content.splice(index,1);
                          
                          setContent(content.filter((elem)=>{
                            return elem !== e.target.innerText
                          }));

                          setInputContent(e.target.innerText);
                        }} key={index} style={contentLi}>{val}</li>
                      })
                    }
                  </ul>
                </div>
                <div className='form'>
                    <input onKeyPress={(e)=>{
                      if(e.key === 'Enter' && e.target.value){
                        e.preventDefault();
                        info.content.push(e.target.value);
                        setContent([...content,e.target.value])
                        setInputContent("")
                      }
                    }} onChange={(e)=>{
                      setInputContent(e.target.value)
                    }} value={inputContent} type='text' className="form-control" placeholder='Enter a point of content and press enter'/>
                </div>
              </div>

              {/* submit */}
              <Button id="btn" variant="contained" className='mt-2' type="submit"><b>SUBMIT</b></Button>
            </form>
          </div>
        </>
      ) : (
        <Error />
      )}
    </>
  );
}

export default CreateProduct;
