import React,{useState,useEffect} from 'react';
import {useParams,useHistory} from 'react-router-dom';
import ReactDOM from "react-dom";
import axios from 'axios';

const PayPalButton = window.paypal.Buttons.driver("react", { React, ReactDOM });

function Payment() {
    const {orderId} = useParams();
    const history = useHistory();

    const [info,setInfo] = useState({});
    useEffect(() => {
        const getAmount = async() =>{
            const res = await axios.get("/get-amount/"+orderId);
            setInfo({name:res.data.customer_name,amount:res.data.total_price,email:res.data.customer_email});
            console.log(res);
        }
        getAmount();
    }, [orderId])

    const createOrder = (data, actions) =>{
        return actions.order.create({
          purchase_units: [
            {
              amount: {
                value: info.amount,
              },
            },
          ],
        });
      };

    const onApprove = (data, actions) => {
        console.log("Successfully payment");
        axios.post("/success-payment",{
            id:orderId,
            email:info.email
        }).then((res)=>{
            if(res.data === "okay"){
                alert("Thanks for ordering. Everything is fine. We'll contact you soon.");
                history.push("/");
                return actions.order.capture();
            }
        })
    };

    return (
        <div className='containers bg-light' style={{height:"100vh",display:"grid",placeItem:"center"}}>
            <div>
                <h1 className="text-center fw-bold">Hi, {info.name}</h1>
                <h3 className="text-center">Pay with paypal</h3>
            </div>
            <PayPalButton
                createOrder={(data, actions) => createOrder(data, actions)}
                onApprove={(data, actions) => onApprove(data, actions)}
            />
        </div>
    )
}

export default Payment
