import React from "react";
import { Link } from "react-router-dom";
import ReactTooltip from "react-tooltip";
import Rating from "./Rating";

function Item({
  id,
  title,
  img,
  achive,
  remains,
  ratings,
  allRating,
  prices,
}) {
  const rating = parseFloat(ratings);
  const available = parseInt(remains);
  const link = `/product/${id}`;
  const price = parseFloat(prices);
  const dollar = Math.floor(price);
  const cent = (price - Math.floor(price)).toFixed(2).toString().split(".")[1] || '00';

  return (
    <div className="product_item position-relative">
      {achive !== 'None' ? (
        achive === "bext seller" ? (
          <div className="achive best">
            <small>Best Seller</small>
          </div>
        ) : (
          <div className="achive choice">
            <small>
              Amazon's <small style={{ color: "#F69931" }}>Choice</small>
            </small>
          </div>
        )
      ) : (
        ""
      )}
      <Link to={link} className="d-block">
        <img className="d-block w-100" src={img} alt="Product" />
      </Link>
      <div className="product_detail mt-2">
        <Link to={link} className="title" data-tip={title}>
          {title}
          <ReactTooltip className="tooltip" delayShow={100} />
        </Link>
        <div className="d-flex" style={{ gap: "5px", marginTop: "5px" }}>
          <Rating rating={rating}/>
          <span style={{ color: "#006dad" }}>Total {allRating} ratings</span>
        </div>
        <div
          className="d-flex align-items-center"
          style={{ marginTop: "5px", gap: "8px" }}
        >
          <div className="d-flex">
            <span>$</span>
            <span style={{ fontSize: "25px", lineHeight: "28px" }}>
              {dollar}
            </span>
            <span>.{cent}</span>
          </div>
        </div>
        <div>
          {available !== 0 ? (
            <small style={{ color: "#686868" }}>
              {available} items available
            </small>
          ) : (
            <small style={{ color: "rgb(255, 30, 30)", fontWeight: "bold" }}>
              Sorry, {available} item available!
            </small>
          )}
        </div>
      </div>
    </div>
  );
}

export default Item;
