import React from 'react'

function RatePercent({star,percent}) {
    return (
        <div className='RatePercent d-grid'>
            <span>{star} star</span>
            <div className="progress w-100" style={{height:"20px"}}>
                <div className="progress-bar" role="progressbar" style={{width: `${percent}%`}} aria-valuenow="25" aria-valuemin="0" aria-valuemax="100"></div>
            </div>
            <span className='text-end'>{percent}%</span>
        </div>
    )
}

export default RatePercent
