import React from 'react';
import {Link} from 'react-router-dom';

function Card(props) {
    return (
        <div className='card_category'>
            <h5 className='fw-bold'>{props.title}</h5>
            <Link to={props.link} className="d-block">
                <img src={props.pic} alt="Category" style={{objectFit:'cover'}} className="d-block mx-auto" height='300px' width='100%'/>
            </Link>
            <Link className='mt-2 link' to={props.link}>See more</Link>
        </div>
    )
}

export default Card
