import React from "react";
import emptyImg from "../img/cart_empty.svg";
import { Link } from "react-router-dom";

function EmptyCart() {
  return (
    <div
      id="emptyCart"
      className="d-flex align-items-center bg-light"
      style={{ gap: "20px", padding: "50px 30px" }}
    >
      <img
        src={emptyImg}
        alt="Empty"
        className="d-block"
        style={{ width: "300px" }}
      />
      <div>
        <h2 className="fw-bold">Your Amazon Cart is empty</h2>
        <Link className="link" to="/">
          Shop Today's Deal
        </Link>
        <div className="mt-3">
          <Link to="/login" style={{ fontSize: "18px" }} className="btns2">
            Sign in to your account
          </Link>
          <Link
            to="/signup"
            style={{
              padding: "4px 12px",
              border: "1px solid #cfcfcf",
              color: "#2c2c2c",
              borderRadius: "10px",
              fontSize: "18px",
              marginLeft: "10px",
            }}
          >
            Sign up now
          </Link>
        </div>
      </div>
    </div>
  );
}

export default EmptyCart;
