import React, { useEffect, useState } from 'react';
import Nav from '../componants/Nav';
import axios from 'axios';
import Load from '../componants/Load';
import Error from './Error';
import AccountList from '../componants/AccountList';
import Footer from '../componants/Footer';

function Account() {
    const [auth,setAuth] = useState('');
    useEffect(()=>{
        axios.get('/auth').then((info)=>{
            setAuth(info.data);
            const load_load = document.querySelector('.load_load');
            load_load.style.animation = 'load_end 0.7s linear';
            setTimeout(() => {
                load_load.classList.add('d-none');
            }, 680);
        }).catch((err)=>{
            console.log(err);
        })
    },[]);

    return (
        <>
            <Load/>
            {
                auth?
                    <>
                        <Nav auth={auth}/>
                        <div className="containers mb-5">
                            <div style={{maxWidth:"950px",margin:"auto"}}>
                                <h2 className='fw-bold mb-4'>Your Account</h2>
                                <AccountList userId={auth._id}/>
                            </div>
                        </div>
                        <Footer/>
                    </> :
                    <Error/>
            }
        </>
    )
}

export default Account
