import React, { useEffect, useState } from "react";
import Nav from "../componants/Nav";
import Banner from "../componants/Banner";
import Card from "../componants/Card";
import Footer from "../componants/Footer";
import "../css/index.css";
import { Link } from "react-router-dom";
import Silde from "../componants/Silde";
import SlideItems from "../componants/SlideItems";
import EmojiEmotionsIcon from "@mui/icons-material/EmojiEmotions";
import ShoppingCartIcon from "@mui/icons-material/ShoppingCart";
import AutoAwesomeIcon from "@mui/icons-material/AutoAwesome";
import StoreIcon from "@mui/icons-material/Store";
import Load from "../componants/Load";
import axios from "axios";

function Home() {
  const [category, setCategory] = useState([]);
  const [randomProducts,setRandomProducts] = useState([]);
  const [slideProducts,setSlideProducts] = useState([]);

  const [auth, setAuth] = useState("");
  useEffect(() => {
    document.body.classList.add("no-scroll");
    document.querySelector("html").style.scrollBehavior = 'initial';
    axios
      .get("/auth")
      .then(async (info) => {
        setAuth(info.data);

        try {
          const res = await axios.get("/get-category");
          const res2 = await axios.get('/get-random-products');
          setCategory(res.data);
          setRandomProducts(res2.data.products1);
          setSlideProducts(res2.data.products2);

          window.scrollTo(0,0)
          const load_load = document.querySelector(".load_load");
          load_load.style.animation = "load_end 0.7s linear";
          document.body.classList.remove("no-scroll");
          setTimeout(() => {
            document.querySelector("html").style.scrollBehavior = 'smooth';
            load_load.classList.add("d-none");
          }, 700);
        } catch (error) {
          console.log(error);
        }
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  return (
    <>
      <Load />
      <Nav auth={auth} />
      <Banner />
      <section id="category" className="d-grid containers">
        {category.map((elem,index) => {
          return (
            <Card
              key={index}
              title={elem.name}
              pic={`/images/${elem.photo}`}
              link={`/category/${elem.name}`}
            />
          );
        })}
      </section>
      <Silde products={slideProducts}/>
      <SlideItems items={randomProducts} />
      {auth ? (
        <section id="sign_bottom" className="bg-light mt-4">
          <div>
            <p style={{ color: "#a923e1", fontWeight: "bold" }}>
              <AutoAwesomeIcon /> You're now a honorable member of Amazon{" "}
              <EmojiEmotionsIcon />
            </p>
            <b>
              <ShoppingCartIcon /> Visit and shop amazing items! <StoreIcon />
            </b>
          </div>
        </section>
      ) : (
        <section id="sign_bottom" className="bg-light mt-4">
          <div>
            <span>See personalized recommendations</span>
            <Link to="/login" className="btns">
              Sign in
            </Link>
            <small>
              New customer?{" "}
              <Link className="link" to="/signup">
                <small>Start here.</small>
              </Link>
            </small>
          </div>
        </section>
      )}
      <Footer />
    </>
  );
}

export default Home;
