import React,{useState,useEffect} from 'react';
import axios from 'axios';
import Load from '../componants/Load';
import Error from './Error';
import logo from '../img/logo2.png';
import { Link } from 'react-router-dom';
import {useHistory} from 'react-router-dom';

function ChangeEmail() {
    const history = useHistory();
    const [email,setEmail] = useState();
    const [auth,setAuth] = useState('');

    useEffect(()=>{
        axios.get('/auth').then((info)=>{
            setAuth(info.data);
            const load_load = document.querySelector('.load_load');
            load_load.style.animation = 'load_end 0.7s linear';
            setTimeout(() => {
                load_load.classList.add('d-none');
            }, 680);
        }).catch((err)=>{
            console.log(err);
        })
    },[]);

    const alterEmail = (e) =>{
        e.preventDefault();
        axios.post('/change-email',{
            id: auth._id,
            newEmail: email,
        }).then((res)=>{
            if(res.data === 'Good to go'){
                history.push('/email-verify-change');
            }else{
                alert('This email is already registered. Try with another one');
                setEmail('');
                const input = document.getElementById('input');
                input.focus();
            }
        })
    }

    return (
        <>
            <Load/>
            {
                auth?
                    <>
                        <section id="login">
                            <div className="text-center mb-3">
                                <Link to='/'>
                                    <img width='150px' src={logo} alt="logo" />
                                </Link>
                            </div>
                            <h2>Change your email address</h2>
                            <p>Current email address: <b>{auth.email}</b></p>
                            <p>Enter the new email address you would like to associate with your account below. We will send a One Time Password (OTP) to that address.</p>
                            <form onSubmit={alterEmail} className='form' style={{gap:"5px"}}>
                                <b className="d-block">New email address</b>
                                <input id='input' value={email} onChange={(e)=>{
                                    setEmail(e.target.value)
                                }} type="email" required/>
                                <button className="btns mt-2">Continue</button>
                            </form>
                        </section>
                        <small className="text-secondary d-block text-center mt-3">© 1996-2021, Amazon.com, Inc. or its affiliates</small>
                    </>
                    :
                    <Error/>
            }
        </>
    )
}

export default ChangeEmail
