import React from 'react';
import banner1 from '../img/bannar1.jpg';
import banner2 from '../img/bannar2.jpg';
import banner3 from '../img/bannar3.jpg';
import banner4 from '../img/bannar4.jpg';
import banner5 from '../img/bannar5.jpg';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';

function Banner() {
    return (
        <section id='hero_home' className='position-relative'>
            <div className="containers_banner">
                <div id="carouselExampleControls" className="carousel slide" data-bs-ride="carousel">
                    <div className="carousel-inner">
                        <div className="carousel-item active">
                            <img src={banner1} className="d-block w-100" alt="Banner"/>
                        </div>
                        <div className="carousel-item">
                            <img src={banner2} className="d-block w-100" alt="Banner"/>
                        </div>
                        <div className="carousel-item">
                            <img src={banner3} className="d-block w-100" alt="Banner"/>
                        </div>
                        <div className="carousel-item">
                            <img src={banner4} className="d-block w-100" alt="Banner"/>
                        </div>
                        <div className="carousel-item">
                            <img src={banner5} className="d-block w-100" alt="Banner"/>
                        </div>
                    </div>
                </div>
                <button className="banner_control prev" type="button" data-bs-target="#carouselExampleControls" data-bs-slide="prev">
                    <ChevronLeftIcon className='arrow-icon'/>
                </button>
                <button className="banner_control next" type="button" data-bs-target="#carouselExampleControls" data-bs-slide="next">
                    <ChevronRightIcon className='arrow-icon'/>
                </button>
            </div>
        </section>
    )
}

export default Banner;
