import React,{useEffect, useState,useRef} from 'react';
import Rating from './Rating';
import moment from "moment";
import axios from 'axios';
import { useHistory } from 'react-router-dom';

function Comment({commentInfo,userId,userHelp}) {
    const history = useHistory();

    const [helpedful,setHelpedful] = useState(false);
    
    const userHelpful = userHelp || [];

    useEffect(()=>{
        userHelpful.map((val)=>{
            if(val === commentInfo._id){
                setHelpedful(true)
            }
            return ''
        });
    },[]);

    const people = useRef();

    return (
        <div className='Comment'>
           <div className='d-flex align-items-center mb-2'>
                <img style={{width:"40px",objectFit:"cover",height:"40px"}} src="https://images-na.ssl-images-amazon.com/images/S/amazon-avatars-global/default._CR0,0,1024,1024_SX48_.png" alt="profile_pic" />
                <h4 className='fw-bold ms-3 mb-0'>{commentInfo.reviewer_name}</h4>
           </div> 
           <div className="d-flex">
               <Rating rating={commentInfo.rating}/>
               <b className='ms-2'>{commentInfo.heading}</b>
           </div>
            <p className="text-secondary mb-1">Reviewed on {moment(commentInfo.date).format('LL')}</p>
            <p className='mb-2'>
                {commentInfo.comment}
            </p>
            <p className='text-secondary' ref={people}>{commentInfo.helpful} people found this helpful</p>
            <div>
                {
                    !helpedful ? 
                    <button className='btns4' onClick={async(e)=>{
                        if(userId){
                            e.target.disabled = true;
                            e.target.innerText = "Loading.."
                            
                            await axios.post("/helpful",{
                                id:commentInfo._id,
                                helpful:commentInfo.helpful,
                                userId:userId,
                                userHelpful
                            });
        
                            setHelpedful(true);

                            people.current.innerText = `${commentInfo.helpful + 1} people found this helpful`;
                        }else{
                            history.push("/login");
                        }
                    }}>Helpful</button> :
                    <span style={{color:"#4e4e4e"}} className="d-inline-block">You're helped</span>
                }
                <button className='btns4 ms-3'>Report</button>
            </div>
        </div>
    )
}

export default Comment
