import React,{useState} from 'react';
import Button from '@mui/material/Button';
import axios from 'axios';

function AdminLogin({onSuccess}) {
    const [input,setInput] = useState({
        id:"",
        pass:""
    });

    const [disabled,setDisabeld] = useState(true);
    const submit = (e) =>{
        e.preventDefault();
        setDisabeld(true)
        axios.post('/admin',{
            input
        }).then((res)=>{
            setDisabeld(false)
            if(res.data === 'okay'){
                alert("Successfully logged in");
                onSuccess(true);
            }else{
                alert("Invalid Details. Try again");
            }
        }).catch((err)=>{
            console.log(err);
        })
    }
    return (
        <div style={{maxWidth:"600px",background:"#fff",margin:"auto",padding:"20px"}}>
            <h1 className='text-center'>Amazon Admin Login</h1>
            <form onSubmit={submit}>
                <input onChange={(e)=>{
                    setInput({
                        ...input,
                        id:e.target.value
                    });

                    if(e.target.value && input.pass){
                        setDisabeld(false)
                    }else{
                        setDisabeld(true)
                    }
                }} value={input.id} placeholder="Enter ID" type="text" className='form-control mb-2' style={{width:"80%",margin:"auto"}}/>
                <input onChange={(e)=>{
                    setInput({
                        ...input,
                        pass:e.target.value
                    });

                    if(input.id && e.target.value){
                        setDisabeld(false)
                    }else{
                        setDisabeld(true)
                    }
                }} value={input.pass} placeholder="Enter password" type="password" className='form-control' style={{width:"80%",margin:"auto"}}/>
                {
                    disabled ?
                    <Button disabled style={{margin:"auto"}} className='d-block mt-2' type='submit' variant="contained">Submit</Button>
                    :
                    <Button style={{margin:"auto"}} className='d-block mt-2' type='submit' variant="contained">Submit</Button>
                }
            </form>
        </div>
    )
}

export default AdminLogin
