import React, { useEffect, useState, useRef } from "react";
import Nav from "../componants/Nav";
import { Link } from "react-router-dom";
import CartSug from "../componants/CartSug";
import Footer from "../componants/Footer";
import EmptyCart from "../componants/EmptyCart";
import CartList from "../componants/CartList";
import SlideItems from "../componants/SlideItems";
import axios from "axios";
import Load from "../componants/Load";

function Cart() {
  const [auth, setAuth] = useState("");
  const [totalBill, setTotalBill] = useState(0);
  const [refresh, setRefresh] = useState(false);

  useEffect(() => {
    axios
      .get("/auth")
      .then(async (info) => {
        setAuth(info.data);
        if (info.data) {
          let total = 0;
          info.data.cart.map((product) => {
            total += parseFloat(product.price) * product.quantity;
            console.log(total);
            return "";
          });
          setTotalBill(parseFloat(total.toFixed(2)));
        }

        const res2 = await axios.get("/get-random-products-only");
        setRandomProducts(res2.data);

        const load_load = document.querySelector(".load_load");
        load_load.style.animation = "load_end 0.7s linear";
        setTimeout(() => {
          load_load.classList.add("d-none");
        }, 680);
      })
      .catch((err) => {
        console.log(err);
      });
  }, [refresh]);

  const total = useRef();

  const [randomProducts, setRandomProducts] = useState([]);

  const refreshPage = () => {
    const load_load = document.querySelector(".load_load");
    load_load.classList.remove("d-none");
    load_load.style.animation = "";
    setRefresh(!refresh);
  };

  return (
    <>
      <Load />
      <Nav auth={auth} />
      <section className="containers d-grid align-items-center cartPage_main">
        {auth ? (
          <CartList
            totalBill={totalBill}
            cart={auth.cart}
            userId={auth._id}
            refreshPage={refreshPage}
          />
        ) : (
          <EmptyCart />
        )}
        <div>
          {!auth.cart ? (
            ""
          ) : (
            <div className="bg-light mb-3" style={{ padding: "20px" }}>
              <p style={{ fontSize: "18px" }}>
                Subtotal{" "}
                {auth.cart.length === 1
                  ? "(1 item)"
                  : `(${auth.cart.length} items)`}
                :{" "}
                <b ref={total} style={{ fontSize: "18px" }}>
                  ${totalBill}
                </b>
              </p>
              {auth.cart.length ? (
                <Link to="/place-order" className="btns">
                  Proceed to checkout
                </Link>
              ) : (
                <button className="btns w-100" disabled>
                  Proceed to checkout
                </button>
              )}
            </div>
          )}
          <div className="bg-light book_sug" style={{ padding: "15px" }}>
            <b className="d-block mb-3">Deals in magazine subscriptions</b>
            <div className="d-grid" style={{ gap: "15px" }}>
              <CartSug
                photo="https://images-na.ssl-images-amazon.com/images/I/51QZvQ1TPxL._AC_UL100_SR100,100_.jpg"
                title="National Geographic Megazine"
                category="Print Megazine"
                categoryLink="/"
                price="10.00"
                link="/"
                rating={4.5}
                allRating={8000}
              />
              <CartSug
                photo="https://images-na.ssl-images-amazon.com/images/I/61mHopmdFaL._AC_UL100_SR100,100_.jpg"
                title="Muse"
                category="Print Megazine"
                categoryLink="/"
                price="24.99"
                link="/"
                rating={4}
                allRating={8500}
              />
              <CartSug
                photo="https://images-na.ssl-images-amazon.com/images/I/51kgcH-FYhL._AC_UL100_SR100,100_.jpg"
                title="Faces"
                category="Print Megazine"
                categoryLink="/"
                price="24.99"
                link="/"
                rating={5}
                allRating={8500}
              />
              <CartSug
                photo="https://images-na.ssl-images-amazon.com/images/I/916wUzFmptL._AC_UL100_SR100,100_.jpg"
                title="Ask"
                category="Print Megazine"
                categoryLink="/"
                price="24.99"
                link="/"
                rating={5}
                allRating={8500}
              />
            </div>
          </div>
        </div>
      </section>
      <SlideItems items={randomProducts} />
      <Footer />
    </>
  );
}

export default Cart;
