import React,{useEffect,useState} from 'react';
import Nav from '../componants/Nav';
import {useHistory, useParams} from 'react-router-dom';
import axios from 'axios';
import Load from '../componants/Load';
import StarOutlineIcon from "@mui/icons-material/StarOutline";
import StarIcon from "@mui/icons-material/Star";
import ErrorOutlineIcon from '@mui/icons-material/ErrorOutline';
import LoginCom from '../componants/LoginCom';

function CreateReview() {
    const [refresh,setRefresh] = useState(false);

    const {productId} = useParams();
    const history = useHistory();

    const [auth,setAuth] = useState('');
    useEffect(()=>{
        axios.get('/auth').then((info)=>{
            setAuth(info.data);
            const load_load = document.querySelector('.load_load');
            load_load.style.animation = 'load_end 0.7s linear';
            setTimeout(() => {
                load_load.classList.add('d-none');
            }, 680);
        }).catch((err)=>{
            console.log(err);
        })
    },[refresh]);

    const [rating,setRating] = useState(0);

    const [error,setError] = useState(false);
    const [errMsg,setErrMsg] = useState("");

    const submitReview = (e) =>{
        e.preventDefault();

        const heading = document.getElementById('heading');
        const comment = document.getElementById('comment');
        const btn = document.querySelector('.btns');

        if(heading.value && comment.value && rating){
            btn.disabled = true;
            setError(false);
            
            axios.post("/create-review",{
                name:auth.name,
                email:auth.email,
                heading:heading.value,
                rating:rating,
                comment:comment.value,
                productId:productId
            }).then((res)=>{
                btn.removeAttribute('disabled');
                history.push("/product/"+productId);
            }).catch((err)=>{
                console.log(err);
            })

        }else{
            if(!heading.value){
                setError(true);
                setErrMsg("Please, type a heading");
                heading.focus();
            }
            else if(!rating){
                setError(true);
                setErrMsg("Please, give a rating");
            }
            else if(!comment.value){
                setError(true);
                setErrMsg("Please, type a comment");
                comment.focus();
            }
        }
    }

    const onLogin = () =>{
        setRefresh(!refresh)
    }

    return (
        <>
            <Load/>
            {
                auth ? 
                    <>
                        <Nav auth={auth}/>
                        <div className="containers bg-light border mx-auto" style={{maxWidth:"500px"}}>
                            <h2 className='text-center fw-bold'>Write Your review</h2>
                            <p className='text-center'><b>Product ID : {productId}</b></p>
                            {
                                error ?
                                <div className="errorMsg mb-2">
                                    <ErrorOutlineIcon style={{fontSize:"30px",marginRight:"5px"}}/>
                                    <span>{errMsg}</span>
                                </div> : ""
                            }
                            <form className='form' onSubmit={submitReview}>
                                {/* heading */}
                                <div>
                                    <label htmlFor="heading" className='fw-bold mb-1'>Heading</label>
                                    <input className='form-control' type="text" id='heading' placeholder='Enter a heading of review'/>
                                </div>
                                {/* Rating */}
                                <div className='text-center'>
                                    <span className='fw-bold d-block'>Give a rating to this product</span>
                                    <span className='d-inline-block'>
                                        <span className="d-inline-block" onClick={()=>{
                                        setRating(1);
                                        }}>
                                            {
                                                rating > 0 ?
                                                <StarIcon style={{fontSize:"40px"}} className="star"/>:
                                                <StarOutlineIcon style={{fontSize:"40px"}} className="star"/>
                                            }
                                        </span>
                                        <span className="d-inline-block" onClick={()=>{
                                            setRating(2);
                                        }}>
                                            {
                                                rating > 1  ?
                                                <StarIcon style={{fontSize:"40px"}} className="star"/>:
                                                <StarOutlineIcon style={{fontSize:"40px"}} className="star"/>
                                            }
                                        </span>
                                        <span className="d-inline-block" onClick={()=>{
                                            setRating(3);
                                        }}>
                                            {
                                                rating > 2 ?
                                                <StarIcon style={{fontSize:"40px"}} className="star"/>:
                                                <StarOutlineIcon style={{fontSize:"40px"}} className="star"/>
                                            }
                                        </span>
                                        <span className="d-inline-block" onClick={()=>{
                                            setRating(4);
                                        }}>
                                            {
                                                rating > 3 ?
                                                <StarIcon style={{fontSize:"40px"}} className="star"/>:
                                                <StarOutlineIcon style={{fontSize:"40px"}} className="star"/>
                                            }
                                        </span>
                                        <span className="d-inline-block" onClick={()=>{
                                            setRating(5);
                                        }}>
                                            {
                                                rating > 4 ?
                                                <StarIcon style={{fontSize:"40px"}} className="star"/>:
                                                <StarOutlineIcon style={{fontSize:"40px"}} className="star"/>
                                            }
                                        </span>
                                    </span>
                                </div>
                                {/* comment */}
                                <div>
                                    <label htmlFor="comment" className='fw-bold mb-1'>Comment</label>
                                    <textarea className='form-control' rows="5" id='comment' placeholder='Enter a comment of review'></textarea>
                                </div>
                                <button className='btns' type='submit'>SUBMIT</button>
                            </form>
                        </div>
                    </> :
                    <LoginCom onLogin={onLogin}/>
            }
        </>
    )
}

export default CreateReview
