import React,{useEffect, useState} from 'react';
import logo from '../img/logo2.png';
import {useHistory,Link, useParams} from 'react-router-dom';
import axios from 'axios';
import Load from '../componants/Load';
import Error from '../pages/Error';
import ErrorOutlineIcon from '@mui/icons-material/ErrorOutline';

function ResetPass() {
    const history = useHistory();

    const {id} = useParams();
    const [exist,setExist] = useState(false);
    const [input,setInput] = useState({
        pass:"",
        repass:""
    })

    useEffect(()=>{
        const getUser = async() =>{
            try {
                const res = await axios.get("/get-email/"+id);
                if(res.data){
                    setExist(true)
                }else{
                    setExist(false)
                }
                const load_load = document.querySelector('.load_load');
                load_load.style.animation = 'load_end 0.7s linear';
                setTimeout(() => {
                    load_load.classList.add('d-none');
                }, 680);
            } catch (error) {
                console.log(error);
            }
        }
        getUser();
    },[id]);

    const [error,setError] = useState(false);
    const [prob,setProb] = useState('');

    const reset = async(e) =>{
        e.preventDefault();
        const pass = document.getElementById("new");
        const re = document.getElementById("re");

        if(!input.pass || !input.repass){
            setError(true);
            if(!input.repass){
                setProb("Re-enter the password")
                re.focus();
            }
            if(!input.pass){
                setProb("Enter a new password")
                pass.focus();
            }
        }else{
            if(input.pass.length >= 6){
                if(input.pass === input.repass){
                    setError(false)
                    // actual success
                    try {
                        const res = await axios.post("/reset-password-forget",{
                            id:id,
                            pass:input.pass,
                        });
                        if(res.data === 'okay'){
                            alert("Your password has been changed");
                            history.push("/login");
                        }
                    } catch (error) {
                        console.log(error);
                    }
                }else{
                    setError(true)
                    setProb("Confirm password is not matched")
                    re.focus();
                }
            }else{
                setError(true);
                setProb("Password can't be less than 6 characters");
                pass.focus();
            }
        }
    }

    return (
        <>
            <Load/>
            {
                exist ?
                    <section id="login">
                        <div className="d-block text-center">
                            <Link to='/'>
                                <img width='150px' src={logo} alt="logo" className="mb-2" />
                            </Link>
                        </div>
                        {
                            error?
                            <div className="warning mb-3">
                                <ErrorOutlineIcon className='arrow-icon' />
                                <div>
                                    <h3>There was a problem</h3>
                                    <span>{prob}</span>
                                </div>
                            </div>: ""
                        }
                        <h2>Create new password</h2>
                        <p>We'll ask for this password whenever you Sign-In.</p>
                        <form className="form" onSubmit={reset}>
                            <div>
                                <label htmlFor="new" className='mb-1'>New password</label>
                                <input onChange={(e)=> setInput({...input,pass:e.target.value})} value={input.pass} type="password" id='new' className='form-control' placeholder='Enter your new password'/>
                                <small className="text-secondary">Passwords must be at least 6 characters.</small>
                            </div>
                            <div>
                                <label htmlFor="re" className='mb-1'>Re-enter password</label>
                                <input onChange={(e)=> setInput({...input,repass:e.target.value})} value={input.repass} type="password" id='re' className='form-control' placeholder='Enter the password again'/>
                            </div>
                            <button className="btns" type='submit'>Continue</button>
                        </form>
                    </section>
                    : 
                    <Error/>
            }
        </>
    )
}

export default ResetPass
