import React,{useState} from 'react';
import axios from 'axios';
import Load from '../componants/Load';
import Error from './Error';
import Button from '@mui/material/Button';
import {Link} from 'react-router-dom';

// <styles>

const heading = {background:"#3189e3",color:"#fff", textAlign:"center",padding:"10px"};

// </styles>

function CreateCategory() {
    const [info,setInfo] = useState({
        name:"",
        desc:"",
    });
    const [photo,setPhoto] = useState();
    const [admin,setAdmin] = useState(false);

    axios.get("/get-admin")
        .then((res)=>{
            const load_load = document.querySelector('.load_load');
            load_load.style.animation = 'load_end 0.7s linear';
            setTimeout(() => {
                load_load.classList.add('d-none')
            }, 680);
            if(res.data === "okay"){
                setAdmin(true)
            }else{
                setAdmin(false)
            }
        }).catch((err)=>{
            console.log(err);
        })

    const createCat = async(e) =>{
        e.preventDefault();

        const formData = new FormData();
        formData.append("photo",photo);

        const btn = document.getElementById('btn');
        btn.disabled = true;

        try {
            const res = await axios.post('/add-category',{
                info
            });
            if(res.data !=='name error'){
                const res2 = await axios.post('/add-photo-category?id='+res.data,formData,{
                    headers:{
                        "Content-type":"multipart/form-data"
                    }
                });
                
                btn.removeAttribute('disabled');
                console.log(res2);
                alert("This category has been created");

                setInfo({
                    name:"",
                    desc:""
                });
                setPhoto('')
                const photoInput = document.getElementById('photo');
                photoInput.value = ''
            }else{
                console.log("name error");
                btn.removeAttribute('disabled');
                alert("This name of category is already exists. Try with other")
            }
            
        } catch (error) {
            btn.removeAttribute('disabled');
            console.log(error);
        }
    }

    return (
        <>
            <Load/>
            {
                admin ?
                    <>
                    
                        <h2 style={heading}>Amazon Admin Panal - Create Category
                        </h2>
                        <div className="px-4 py-2">
                            <Link to='/' className='btn btn-primary'>Visit Amazon</Link>
                            <Link to='/admin/create-product' className='btn btn-primary mx-2'>Create Product</Link>
                            <Link to='/admin' className='btn btn-primary'>Admin Panal</Link>
                        </div>
                        <div className="containers bg-light border" style={{maxWidth:"500px"}}>
                            <form className='form' onSubmit={createCat}>
                                <div>
                                    <label htmlFor='name' className='fw-bold mb-1'>Name of category:</label>
                                    <input onChange={(e)=>{
                                        setInfo({...info, name:e.target.value})
                                    }} id='name' value={info.name} type="text" placeholder='Enter a name of category' className='form-control' required/>
                                </div>
                                <div>
                                    <label htmlFor='photo' className='fw-bold mb-1'>Photo of category:</label>
                                    <input onChange={(e)=>{
                                        setPhoto(e.target.files[0]);
                                    }} id='photo' type="file" className='form-control' required/>
                                </div>
                                <div>
                                    <label htmlFor='desc' className='fw-bold mb-1'>Description of category:</label>
                                    <input onChange={(e)=>{
                                        setInfo({...info, desc:e.target.value})
                                    }} value={info.desc} id='desc' type="text" className='form-control' placeholder='Enter a short description of category' required/>
                                </div>
                                <Button id="btn" type='submit' variant='contained'><b>Create</b></Button>
                            </form>
                        </div>
                    </> :
                    <Error/>
            }
        </>
    )
}

export default CreateCategory
