import React, { useState, useEffect } from "react";
import Nav from "../componants/Nav";
import Item from "../componants/Item";
import Footer from "../componants/Footer";
import Load from "../componants/Load";
import axios from "axios";
import Error from "./Error";

function Search() {
  const [allItems, setAllItems] = useState([]);

  const search = new URLSearchParams(window.location.search).get('keyword');
  const categoryString = new URLSearchParams(window.location.search).get('category');
  const category = categoryString ? categoryString.replace(":and:","&") : '';

  const [auth, setAuth] = useState("");
  useEffect(() => {
    axios
      .get("/auth")
      .then(async (info) => {
        setAuth(info.data);

        try {
          const res = await axios.post("/search-product",{
            keyword:search,
            category
          });
          setAllItems(res.data);
          const load_load = document.querySelector(".load_load");
          load_load.style.animation = "load_end 0.7s linear";
          setTimeout(() => {
            load_load.classList.add("d-none");
          }, 700);
        } catch (error) {
          console.log(error);
        }
      })
      .catch((err) => {
        console.log(err);
      });
  }, [search]);

  return (
    <>
      <Load />
      {
          search && category ?
            <>
                <Nav auth={auth} />
                <section style={{ background: "#fff" }} className="conatinerz">
                    <h2 className="fw-bold mb-3">You have searched for "{search}" in "{category}" category</h2>
                    <span
                    style={{
                        borderRadius: "10px",
                        padding: "10px 20px",
                        border: "1px solid #c3c3c3",
                    }}
                    className="d-block"
                    >
                    Overall {allItems.length} results for <span className="gold">"{search}"</span>
                    </span>
                    <div className="items" style={{ padding: "40px 0" }}>
                    {allItems.length === 0 ? (
                        <span className="text-secondary">
                        Sorry! there is no product available for "{search}"
                        </span>
                    ) : (
                        allItems.map((value, index) => {
                        return (
                            <Item
                            key={index}
                            id={value._id}
                            prices={value.price}
                            allRating={value.allRating}
                            ratings={parseInt(value.rating)}
                            remains={value.available}
                            img={"/images/" + value.photos[0]}
                            title={value.name}
                            achive={value.achivement}
                            />
                        );
                        })
                    )}
                    </div>
                </section>
                <Footer />
            </>
            :
            <Error/>
      }
    </>
  );
}

export default Search;
