import React,{useState,useEffect} from 'react';
import axios from 'axios';
import Error from './Error';
import Load from '../componants/Load';
import Nav from '../componants/Nav';
import Footer from "../componants/Footer";
import moment from 'moment';

const h2Class = "text-center fw-bold mb-3";
const tableStyle = {minWidth:"700px",maxWidth:"900px"};

function MyOrders() {
    const [orders,setOrders] = useState([]);

    const [auth,setAuth] = useState('');
    useEffect(()=>{
        axios.get('/auth').then(async(info)=>{
            setAuth(info.data);

            const res = await axios.post("/your-order",{
                email:info.data.email
            });
            setOrders(res.data);

            const load_load = document.querySelector('.load_load');
            load_load.style.animation = 'load_end 0.7s linear';
            setTimeout(() => {
                load_load.classList.add('d-none');
            }, 680);
        }).catch((err)=>{
            console.log(err);
        })
    },[]);

    return (
        <>
            <Load />
            {
                auth ? 
                    <>
                        <Nav auth={auth} />
                        <div className="containers bg-light mb-4">
                            <h2 className={h2Class}>Your Orders</h2>
                            <div className='scrollbar'>
                                {
                                    orders.length ?
                                        <table class="table text-center mx-auto" style={tableStyle}>
                                            <thead>
                                                <tr>
                                                    <th scope="col">Sno</th>
                                                    <th scope="col">Items</th>
                                                    <th scope="col">Total Price</th>
                                                    <th scope="col">Payment</th>
                                                    <th scope="col">Deliverance</th>
                                                    <th scope="col">Date</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {
                                                    orders.map((order,ind)=>{
                                                        return(
                                                            <tr key={ind}>
                                                                <th scope="row">{ind + 1}</th>
                                                                <td>{order.ordered_products.length}</td>
                                                                <td>${order.total_price}</td>
                                                                <td>{(order.payment).toString()}</td>
                                                                <td>{(order.delivered).toString()}</td>
                                                                <td>{moment(order.Date).fromNow()}</td>
                                                            </tr>
                                                        )
                                                    })
                                                }
                                            </tbody>
                                        </table>
                                        : 
                                        <p className="text-secondary mb-0 text-center p-2" style={{color:"#505050",background:"#ddd"}}>You haven't ordered any items yet.</p>
                                }
                            </div>
                        </div>
                        <Footer/>
                    </>
                    : <Error/>
            }
        </>
    )
}

export default MyOrders
