import axios from 'axios';
import React, { useEffect, useState,useRef } from 'react';
import { Link } from 'react-router-dom';
import ReactTooltip from "react-tooltip";

const priceStyle = {right:"0",top:"0",fontSize:"22px",fontWeight:"bold"};

function CartItem({link,price,quantity,img,title,cartId,userId,refreshPage}) {
    const [disabled,setDisabled] = useState(true);
    const quanInput = useRef();
    useEffect(()=>{
        quanInput.current.defaultValue = quantity;
    })

    return (
        <div className='cart-item d-flex'>
            <Link to={link} style={{flexBasis:"20%",textAlign:"center"}}>
                <img style={{objectFit:"contain"}} src={img} alt="Product" />
            </Link>
            <div>
                <Link to={link} className="title" style={{fontSize:"18px"}} data-tip={title}>
                    {title} 
                    <ReactTooltip className="tooltip" delayShow={100} />       
                </Link>
                <small className="text-secondary">Eligible for FREE Shipping</small>
                <small className="d-block" style={{color:"#c45500"}}>Usually ships within 6 to 10 weeks.</small>
                <small className="d-block"><small className="fw-bold">Subscription Length</small>: 1 year auto-renewal</small>
                <div className="d-flex align-items-center mt-2" style={{gap:"15px"}}>
                    <form className="d-flex" onSubmit={async(e)=>{
                        e.preventDefault();
                        const btn = e.target.children[1];
                        btn.disabled = true;
                        btn.innerText = "Loading..";
                        //update quantity
                        const res = await axios.post('/change-quantity',{
                            cartId,quantity:parseInt(e.target.children[0].value)
                        });
                        btn.removeAttribute("disabled");
                        btn.innerText = "Update";
                        if(res.data.condition === 'okay'){
                            setDisabled(true)
                            refreshPage();
                        }
                    }}>
                        <input ref={quanInput} onChange={(e)=>{
                            if(e.target.value < 1){
                                e.target.value = 1;
                            }
                            console.log(e.target.value);
                            if(parseInt(e.target.value) === quantity){
                                setDisabled(true)
                            }else{
                                setDisabled(false);
                            }
                        }} type="number" style={{width:"70px",padding:"2px 4px"}}/>
                        <button className="btns2 ms-1 py-1 px-2" style={{fontSize:"11px"}} disabled={disabled}>Update</button>
                    </form>
                    <button className="btns" onClick={async(e)=>{
                        e.target.disabled = true;
                        e.target.innerText = "Loading.."
                        try {
                            const res = await axios.post("/delete-cart",{
                                userId,
                                orderId:cartId
                            });
                            e.target.removeAttribute("disabled");
                            e.target.innerText = 'Delete';
                            if(res.data.result === "okay"){
                                const load_load = document.querySelector(".load_load");
                                load_load.classList.remove("d-none");
                                load_load.style.animation = "";
                                refreshPage();
                            }
                        } catch (error) {
                            console.log(error);
                        }
                    }}>Delete</button>
                </div>
            </div>
            <span className="position-absolute" style={priceStyle}>${parseFloat((price * quantity).toFixed(2))}</span>
        </div>
    )
}

export default CartItem
