import React, { useEffect, useState } from "react";
import axios from "axios";
import Load from "../componants/Load";
import Error from "./Error";
import Nav from "../componants/Nav";
import ShoppingCartIcon from "@mui/icons-material/ShoppingCart";
import { Link } from "react-router-dom";
import Modal from "../componants/Modal";
import { useHistory } from "react-router-dom";

const divClass = "bg-light containers border mt-2 d-grid";
const divStyle = {gridTemplateColumns:"1fr 1fr",gap:"30px"};
const itemStyle = { gap: "20px", background: "rgb(233, 233, 233)" ,border:"1px solid #b7b7b7"};
const quality = { gap: "10px", gridTemplateColumns: "1fr 2fr" };
const imgStyle = "d-block border mx-auto";
const parentStyle = { flexBasis: "100%", gap: "5px" };

function PlaceOrder() {
  const history = useHistory();

  const [auth, setAuth] = useState("");
  useEffect(() => {
    axios
      .get("/auth")
      .then((info) => {
        setAuth(info.data);
        setOrderInfo({...orderInfo,name:info.data.name,email:info.data.email,cart:info.data.cart});

        const load_load = document.querySelector(".load_load");
        load_load.style.animation = "load_end 0.7s linear";
        setTimeout(() => {
          load_load.classList.add("d-none");
        }, 680);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  const [orderInfo,setOrderInfo] = useState({
    name:"",
    email:"",
    phone:"",
    country:"",
    city:"",
    area:"",
    zip:"",
    cart:""
  });

  const submitOrder = async() =>{
    try {
      const res = await axios.post("/place-order",{
        orderInfo,
      });
      if(res.data.result === 'okay'){
        history.push("/payment/"+res.data.id);
      }
    } catch (error) {
      console.log(error);
    }
  }

  return (
    <>
      <Load />
      {auth ? (
        auth.cart.length ? (
          <>
            {/* Actual jsx */}
            <Nav auth={auth} />
            <div className={divClass} style={divStyle}>
              <div>
                <h2 className="text-center mb-3">
                  Enter info and place order <ShoppingCartIcon fontSize="large" />
                </h2>
                <form style={{maxWidth:"500px"}} className="form mx-auto" onSubmit={(e)=>{
                  e.preventDefault();
                  const modalOpen = document.getElementById("modalOpen");
                  modalOpen.click();
                }}>
                  <div>
                    <label htmlFor="name" className="fw-bold">
                      Full name (First and Last name)
                    </label>
                    <input
                      id="name"
                      type="text"
                      className="form-control"
                      placeholder="Enter your name"
                      required
                      onChange={(e)=>{
                        setOrderInfo({...orderInfo,name:e.target.value});
                      }}
                      value={orderInfo.name}  
                    />
                  </div>
                  <div>
                    <label htmlFor="phone" className="fw-bold">
                      Phone number (with country code)
                    </label>
                    <input
                      id="phone"
                      type="text"
                      className="form-control"
                      placeholder="Enter a valid phone number"
                      required
                      onChange={(e)=>{
                        setOrderInfo({...orderInfo,phone:e.target.value});
                      }}
                      value={orderInfo.phone}
                    />
                  </div>
                  <div>
                    <label htmlFor="country" className="fw-bold">
                      Country
                    </label>
                    <input
                      id="country"
                      type="text"
                      className="form-control"
                      placeholder="Enter your country name(EN)"
                      required
                      onChange={(e)=>{
                        setOrderInfo({...orderInfo,country:e.target.value});
                      }}
                      value={orderInfo.country}
                    />
                  </div>
                  <div>
                    <label htmlFor="city" className="fw-bold">
                      City
                    </label>
                    <input
                      id="city"
                      type="text"
                      className="form-control"
                      placeholder="Enter your city name(EN)"
                      required
                      onChange={(e)=>{
                        setOrderInfo({...orderInfo,city:e.target.value});
                      }}
                      value={orderInfo.city}
                    />
                  </div>
                  <div>
                    <label htmlFor="area" className="fw-bold">
                      Area of city
                    </label>
                    <input
                      id="area"
                      type="text"
                      className="form-control"
                      placeholder="Enter street address or area name in your city"
                      required
                      onChange={(e)=>{
                        setOrderInfo({...orderInfo,area:e.target.value});
                      }}
                      value={orderInfo.area}
                    />
                  </div>
                  <div>
                    <label htmlFor="zip" className="fw-bold">
                      Zip code
                    </label>
                    <input
                      id="zip"
                      type="number"
                      className="form-control"
                      placeholder="Enter zip code"
                      required
                      onChange={(e)=>{
                        setOrderInfo({...orderInfo,zip:e.target.value});
                      }}
                      value={orderInfo.zip}
                    />
                  </div>
                  <div className="d-flex">
                    <Link
                      style={{ flexBasis: "100%" }}
                      to="/cart"
                      className="btns4 mt-2"
                    >
                      Go back
                    </Link>
                    <button
                      style={{ flexBasis: "100%" }}
                      type="submit"
                      className="btns mt-2 ms-3"
                    >
                      Submit
                    </button>
                    <button className="d-none" id="modalOpen" data-bs-toggle="modal" data-bs-target="#takeOrder"></button>
                    <Modal onSubmit={submitOrder} id="takeOrder" title="Placing order">
                      <h3 className="mb-3 fw-bold">Are you sure you want to take this order?</h3>
                      <h5>Before placing this order, please checkout the credentials and information again that you have entered. Thank you</h5>
                    </Modal>
                  </div>
                </form>
              </div>
              <div>
                <h2 className="mb-3 text-center">Your Items</h2>
                <div className="d-grid mb-4" style={{ gap: "10px" }}>
                  {/* item for buy */}
                  {
                      auth.cart.map((cart)=>{
                          return(
                              <div className="d-grid p-3" style={itemStyle}>
                              <img
                                className={imgStyle}
                                height="100px"
                                src={"/images/"+cart.photo}
                                alt="product"
                              />
                              <div className="d-grid" style={parentStyle}>
                                  <div className="mb-0 d-grid" style={quality}>
                                      <b>Product Name :</b>
                                      <span>
                                        {cart.name}
                                      </span>
                                      </div>
                                      <div className="mb-0 d-grid" style={quality}>
                                      <b style={{ flexBasis: "30%" }}>Product ID :</b>
                                      <span>{cart.productId}</span>
                                      </div>
                                      <div className="mb-0 d-grid" style={quality}>
                                      <b style={{ flexBasis: "30%" }}>Quantity :</b>
                                      <span>{cart.quantity}</span>
                                      </div>
                                      <div className="mb-0 d-grid" style={quality}>
                                      <b style={{ flexBasis: "30%" }}>Total price :</b>
                                      <span>${parseFloat((cart.price * cart.quantity).toFixed(2))}</span>
                                  </div>
                              </div>
                              </div>
                          )
                      })
                  }
                </div>
              </div>
            </div>
          </>
        ) : (
          <Error />
        )
      ) : (
        <Error />
      )}
    </>
  );
}

export default PlaceOrder;
