import React,{useEffect,useRef} from 'react';
import ChevronLeftIcon from "@mui/icons-material/ChevronLeft";
import KeyboardArrowRightIcon from "@mui/icons-material/KeyboardArrowRight";
import CartSug from "../componants/CartSug";
import { useState } from 'react';

function SlideItems({items,onClick}) {
    const [pageNo,setPageNo] = useState(1);
    const [totalPage,setTotalPage] = useState();
    const allItems = [];
    useEffect(() => {
        const carouselItem = document.querySelectorAll('#carouselProduct .carousel-item');
        if(carouselItem.length){
            carouselItem[0].classList.add('active')
            setTotalPage(carouselItem.length)
        }        
    }, [items]);
    new Array(Math.ceil(items.length / 4)).fill('*').map((_,ind)=>{
        let limit = (ind * 4) + 4;
        let newArr = items.slice(ind * 4,limit);
        allItems.push(newArr);
        return ''
    });

    const right = useRef();
    const left = useRef();

    const next = () =>{
        if(pageNo === totalPage){
            setPageNo(1)
        }else{
            setPageNo(pageNo + 1)
        }

        right.current.classList.add("pointer-none")
        setTimeout(() => {
            right.current.classList.remove("pointer-none")
        }, 600);
    }
    const prev = () =>{
        if(pageNo === 1){
            setPageNo(totalPage)
        }else{
            setPageNo(pageNo - 1)
        }

        left.current.style.pointerEvents = 'none';
        setTimeout(() => {
            left.current.style.pointerEvents = 'all';
        }, 600);
    }
    return (
        <>
        {
            items.length ?
            <>
                <section
                    className="containers bg-light position-relative slide_items"
                >
                    <span className="counting">Page {pageNo} of {totalPage}</span>
                    <div className="slides">
                    <div
                        className="left slider"
                        style={{zIndex:"1"}}
                        data-bs-target="#carouselProduct"
                        data-bs-slide="prev"
                        ref={left}
                        onClick={prev}
                    >
                        <ChevronLeftIcon style={{ fontSize: "25px"}} />
                    </div>
                    <div
                        className="right slider"
                        style={{zIndex:"1"}}
                        data-bs-target="#carouselProduct"
                        ref={right}
                        data-bs-slide="next"
                        onClick={next}
                    >
                        <KeyboardArrowRightIcon style={{ fontSize: "25px"}} />
                    </div>
                    </div>
                    <h4 className="fw-bold mb-4">Some products you might be fascinated</h4>
                    <div
                    id="carouselProduct"
                    className="carousel slide"
                    data-bs-ride="carousel"
                    data-bs-interval="false"
                    >
                    <div className="carousel-inner" >

                        {
                            allItems.map((carousel,index)=>{
                                return(
                                    <div className="carousel-item" key={index}>
                                        <div className="justify-content-evenly">
                                            {
                                                carousel.map((val,ind)=>{
                                                    return(
                                                        <CartSug
                                                            key={ind}
                                                            photo={"/images/"+val.photos[0]}
                                                            title={val.name}
                                                            category={val.category}
                                                            categoryLink={`/category/${val.category}`}
                                                            price={val.price}
                                                            link={"/product/"+val._id}
                                                            rating={val.rating}
                                                            onClick={onClick}
                                                        />
                                                    )
                                                })
                                            }
                                        </div>
                                    </div>
                                )
                            })
                        }
                    </div>
                    </div>
                </section>
                </> : ""
        }
        </>
    )
}

export default SlideItems
