import React,{useRef} from 'react';
import CartItem from './CartItem';

function CartList({cart,totalBill,userId,refreshPage}) {
    const total = useRef();

    return (
        <div id="cartList" className="bg-light" style={{padding:"30px"}}>
            <h2 className="fw-bold">Shopping Cart</h2>
            <p className='text-end' style={{borderBottom:"1px solid #ddd",color:"#333"}}>Price</p>
            <div className="d-grid" style={{gap:"15px"}}>
                {
                    cart.length ? 
                        cart.map((product,index)=>{
                            return(
                                <CartItem
                                 key={index}
                                 title={product.name}
                                 img={"/images/"+product.photo}
                                 link={'/product/'+product.productId}
                                 price={product.price} 
                                 quantity={product.quantity}
                                 cartId={product._id}
                                 userId={userId}
                                 refreshPage={refreshPage}
                                />
                            )
                        }) :
                        <span className="text-secondary">No items to buy. Add to cart your favourite products, dude!</span>
                }
            </div>
            <div className="total mt-3">
                <p className="mb-0 py-1 text-end" style={{borderTop:"1px solid #ddd",fontSize:"18px"}}>
                    Subtotal {cart.length === 1 ? "(1 item)":`(${cart.length} items)`} <b ref={total} style={{fontSize:"18px"}}>${totalBill}</b>
                </p>
            </div>
        </div>
    )
}

export default CartList
