import React, { useState,useEffect } from "react";
import { useParams } from "react-router-dom";
import Nav from "../componants/Nav";
import Item from "../componants/Item";
import Footer from "../componants/Footer";
import Pagination from "../componants/Pagination";
import CircularProgress from '@mui/material/CircularProgress';
import Load from "../componants/Load";
import axios from "axios";

function Category() {
  const [allItems, setAllItems] = useState([]);
  const [count,setCount] = useState();
  const {category} = useParams();
  
  const [auth, setAuth] = useState("");
  useEffect(() => {
    document.body.classList.add("no-scroll");
    document.querySelector("html").style.scrollBehavior = 'initial'
    axios
      .get("/auth")
      .then(async (info) => {
        setAuth(info.data);

        try {
          const res = await axios.get("/categori/"+category);
          setAllItems(res.data.products);
          setCount(res.data.count)
          window.scrollTo(0,0)
          const load_load = document.querySelector(".load_load");
          load_load.style.animation = "load_end 0.7s linear";
          document.body.classList.remove("no-scroll");
          setTimeout(() => {
            document.querySelector("html").style.scrollBehavior = 'smooth';
            load_load.classList.add("d-none");
          }, 700);
        } catch (error) {
          console.log(error);
        }
      })
      .catch((err) => {
        console.log(err);
      });
  }, [category]);

  const [loading,setLoading] = useState(false);
  const displayItem = 6;
  
  const updateItem = async(active) => {
    const pagination = document.querySelector(".pagination");
    pagination.style.pointerEvents = 'none';
    setLoading(true);
    console.log(active);
    let index = 6 * (active - 1);
    let last = index + 6;
    
    const res = await axios.post('/get-product',{
      index,last,category
    });
    setAllItems(res.data)
    pagination.style.pointerEvents = 'all';
    setLoading(false);
  };
  return (
    <>
      <Load/>
      <Nav auth={auth}/>
      {
        loading ? 
          <div className="loading">
             <CircularProgress />
          </div> :
          <section style={{ background: "#fff" }} className="conatinerz">
            <h2 className="text-capitalize fw-bold">{category}</h2>
            <p>
              Shop home entertainment, TVs, home audio, headphones, cameras,
              accessories and more
            </p>
            <span
              style={{
                borderRadius: "10px",
                padding: "10px 20px",
                border: "1px solid #c3c3c3",
              }}
              className="d-block"
            >
              Overall 6000 results for <span className="gold">{category}</span>
            </span>
            <div className="items" style={{ padding: "40px 0" }}>
              {
              allItems.length === 0 ?
                <span className="text-secondary">Sorry! there is no product available in {category}</span> :
                allItems.map((value,index) => {
                  return (
                    <Item
                      key={index}
                      id={value._id}
                      prices={value.price}
                      allRating={value.allRating}
                      ratings={parseInt(value.rating)}
                      remains={value.available}
                      img={"/images/"+value.photos[0]}
                      title={value.name}
                      achive={value.achivement}
                    />
                  );
              })}
            </div>
          </section>
      }
      <Pagination total={Math.ceil(count / displayItem)} onChange={updateItem}/>
      <Footer />
    </>
  );
}

export default Category;
