import React, { useState, useEffect } from "react";
import axios from "axios";
import Load from "../componants/Load";
import Button from "@mui/material/Button";
import { Link } from "react-router-dom";
import AddCircleIcon from "@mui/icons-material/AddCircle";
import VisibilityIcon from "@mui/icons-material/Visibility";
// import EditLocationAltIcon from "@mui/icons-material/EditLocationAlt";
import DeleteForeverIcon from "@mui/icons-material/DeleteForever";
import Table from "../componants/Table";
import AdminLogin from '../componants/AdminLogin';
import ReactTooltip from "react-tooltip";
import Modal from "../componants/Modal";

// <styles>
const iconStyle = {
  background: "#237ae7",
  color: "#fff",
  padding: "3px 5px",
  fontSize: "30px",
  cursor: "pointer",
};
const liStyle =
  "list-group-item d-flex justify-content-between align-items-center shadow";
const categoryTitle = "d-flex align-items-center fw-bold p-2 px-3 border";
const heading = {
  background: "#3189e3",
  color: "#fff",
  textAlign: "center",
  padding: "10px",
};

const noProductStyle = "py-2 px-3 shadow bg-light fw-bold text-danger";
// </styles>

function Admin() {
  const [admin, setAdmin] = useState(false);
  const [refresh,setRefresh] = useState(true);
  const [category, setCategory] = useState([]);
  useEffect(() => {
    const getCategory = async () => {
      try {
        const resp = await axios.get('/get-admin-1');
      
        if (resp.data.result === "okay") {
          const res = await axios.get("/get-category");
          setAdmin(true);

          let categories = res.data;

          categories.map((categ)=>{
            categ.products = [];
            resp.data.products.map((product)=>{
              if(categ.name === product.category){
                categ.products.push(product);
              }
              return '';
            })
            return '';
          })

          setCategory(categories);
          
        } else {
          setAdmin(false);
        }
        const load_load = document.querySelector(".load_load");
        load_load.style.animation = "load_end 0.7s linear";
        setTimeout(() => {
          load_load.classList.add("d-none");
        }, 680);
      } catch (error) {
        console.log(error);
      }
    };

    getCategory();
  }, [refresh,admin]);

  const [show, setShow] = useState(1);

  const onSuccess = (value) =>{
    setAdmin(value)
  }

  return (
    <>
      <Load />
      {admin ? (
        <>
          <h2 style={heading}>Amazon Admin Panal - Overview</h2>
          <div className="px-4 py-2">
              <Link to='/' className='btn btn-primary'>Visit Amazon</Link>
              <Link to='/admin/create-category' className='btn btn-primary mx-2'>Create Category</Link>
              <Link to='/admin/create-product' className='btn btn-primary'>Create Product</Link>
          </div>
          <div className="containers" id="admin_panal">
            <div className="mt-4">
              {/* category */}
              <div className="mb-4 mx-auto" style={{maxWidth:"600px"}}>
                <h3
                  className="mb-3 p-2 text-center"
                  style={{ background: "#ddd" }}
                >
                  All Categories
                </h3>
                {category.map((elem,index) => {
                  return (
                    <>
                      <details className="p-2" key={index}>
                        <summary>
                          <h4
                            className={categoryTitle}
                            style={{ color: "#0669cf", background: "#fff" }}
                          >
                            <AddCircleIcon
                              style={{ fontSize: "26px", marginRight: "10px" }}
                            />{" "}
                            {elem.name}
                          </h4>
                        </summary>
                        <ul className="list-group">
                          {
                            elem.products.map((product,ind)=>{
                              return(
                                <li className={liStyle} key={ind}>
                                  {product.name}
                                  <div className="d-flex ms-2" style={{gap:"5px"}}>
                                    <Link to={"/product/"+product._id}>
                                      <VisibilityIcon style={iconStyle} data-tip="Visit"/>
                                      <ReactTooltip className="tooltip" delayShow={100} />
                                    </Link>
                                    <span className="d-inline-block" data-bs-toggle="modal" data-tip="Delete" data-bs-target={"#deleteProduct"+index+"_"+ind}>
                                      <DeleteForeverIcon style={iconStyle}/>
                                      <ReactTooltip className="tooltip" delayShow={100} />
                                    </span>
                                    <Modal onSubmit={async()=>{
                                      try {
                                        const res = await axios.post("/delete-product",{
                                          id:product._id,
                                        });
                                        console.log(res);
                                        if(res.data === 'okay'){
                                          setRefresh(!refresh)
                                        }
                                      } catch (error) {
                                        console.log(error);
                                      }
                                    }} id={"deleteProduct"+index+"_"+ind} title="Placing order">
                                      <h3 className="mb-3 fw-bold">Are you sure to delete this product?</h3>
                                      <h5>ProductID : {product._id}</h5>
                                      <h5>Product Name : {product.name}</h5>
                                    </Modal>
                                  </div>
                                </li>
                              )
                            })
                          }
                          {
                            !elem.products.length ?
                              <li className={noProductStyle}>No items available for this category</li> : "" 
                          }
                        </ul>
                      </details>
                    </>
                  );
                })}
              </div>

              {/* event */}
              <div>
                <div
                  className="d-flex justify-content-center align-items-center"
                  style={{ gap: "10px" }}
                >
                  <Link style={{ color: "#fff" }} to="/admin/create-product">
                    <Button className="d-block" variant="contained">
                      <span>Add Product</span>
                    </Button>
                  </Link>
                  <Button
                    onClick={() => {
                      setShow(1);
                    }}
                    style={{ background: "#db124a" }}
                    className={show === 1 ? "d-block active" : "d-block"}
                    variant="contained"
                  >
                    <span>New Orders</span>
                  </Button>
                  <Button
                    onClick={() => {
                      setShow(2);
                    }}
                    style={{ background: "#07954d" }}
                    className={show === 2 ? "d-block active" : "d-block"}
                    variant="contained"
                  >
                    <span>Completed Orders</span>
                  </Button>
                </div>
                <div className="mt-4 bg-light p-3 border">
                  {show === 1 ? (
                    <Table orders={"New Orders"} />
                  ) : (
                    <Table orders={"Completed Orders"} />
                  )}
                </div>
              </div>
            </div>
          </div>
        </>
      ) : (
        <AdminLogin onSuccess={onSuccess}/>
      )}
    </>
  );
}

export default Admin;
