import React,{useState} from 'react';
import logo from '../img/logo2.png';
import {useHistory,Link} from 'react-router-dom';
import axios from 'axios';

function ForgetPass() {
    const history = useHistory();
    const [exist,setExist] = useState(true);
    const checkEmail = async(e) =>{
        e.preventDefault();
        const email = document.getElementById("email");
        const btn = document.querySelector(".btns");
        
        btn.disabled = true;
        btn.innerText = "Loading.."
        try {
            const res = await axios.post("/check-user",{
                email:email.value
            });
            console.log(res);
            btn.removeAttribute("disabled")
            btn.innerText = "Continue"
            if(res.data.result === 'okay'){
                setExist(true)
                history.push("/email-verify-forget/"+res.data.id)
            }else{
                setExist(false)
            }
        } catch (error) {
            console.log(error);
        }
    }

    return (
        <>
            <section id="login">
                <div className="d-block text-center">
                    <Link to='/'>
                        <img width='150px' src={logo} alt="logo" className="mb-2" />
                    </Link>
                </div>
                <h2>Password assistance</h2>
                <p>Enter the email address associated with your Amazon account.</p>
                {
                    !exist ?
                    <h5 className='fw-bold text-danger'>This email isn't exist in Amazon</h5>
                    : ""
                }
                <form className="form mb-4" onSubmit={checkEmail}>
                    <div>
                        <label htmlFor="email" className='mb-1'>Email Address</label>
                        <input id='email' className='form-control' type="text" placeholder='Enter you email here' required/>
                    </div>
                    <button type='submit' className='btns'>Continue</button>
                </form>
                <h5>Has your email or mobile number changed?</h5>
                <small className="d-block text-secondary">If you no longer use the email address associated with your Amazon account, you may contact <a href="https://www.amazon.com/gp/help/customer/account-issues/ref=ap_cs_forgot_pwd?ie=UTF8" className='link'><small>Customer Service</small></a> for help restoring access to your account.</small>
            </section>
        </>
    )
}

export default ForgetPass
