import React, { useEffect,useState } from "react";
import axios from "axios";
import {Link} from 'react-router-dom';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';

const itemStyle ={maxWidth:"330px",width:"100%",display:"block",overflow:"hidden",whiteSpace:"nowrap",textOverflow:"ellipsis",color:"#fff14b"};
const proItem = {height:"100px",background:"#81d7f3"};
const ordPro ={height:"100px",overflow:"auto"};
const deli = {height:"55px"}

function Table({orders}) {
  const [newOrders,setNewOrders] = useState([])
  const [prevOrders,setPrevOrders] = useState([]);
  const [refresh,setRefresh] = useState(false);

  useEffect(()=>{
    const getOrders = async() =>{
      const res = await axios.get("/get-all-orders");
      setNewOrders(res.data.newOrders)
      setPrevOrders(res.data.prevOrders);
    }
    getOrders();
  },[refresh]);

  useEffect(()=>{
    const carouselItem = document.querySelectorAll(".admin_table .carousel-item")[0];
    if(carouselItem){
      carouselItem.classList.add("active")
    }
  })

  return (
    <div className="admin_table text-center">
      <div>
        <b>Sno</b>
        <b>Name</b>
        <b>Email</b>
        <b>Country</b>
        <b>City</b>
        <b>Area</b>
        <b>Phone</b>
        <b>Bill</b>
        <b style={proItem}>Products</b>
        <b>Payment</b>
        <b style={deli}>Delivered</b>
      </div>
      <div>
        {
          orders === "New Orders" ?
            <>
              {/* new orders */}
              {
                newOrders.length ? 
                  <>
                    {/* new orders */}
                    <div id="carouselExampleControlsNoTouching" className="carousel slide" data-bs-touch="false" data-bs-interval="false">
                      <div className="carousel-inner">
                          {
                            newOrders.map((order,ind)=>{
                              return(
                                <>
                                  <div className="carousel-item" key={ind}>
                                    <div style={{ verticalAlign: "middle" }} className='new_order rows'>
                                      <b style={{background:"#cd1638"}}>{ind + 1}</b>
                                      <span>{order.customer_name}</span>
                                      <span>{order.customer_email}</span>
                                      <span>{order.country}</span>
                                      <span>{order.city}</span>
                                      <span>{order.area}</span>
                                      <span>{order.customer_phone}</span>
                                      <span>${order.total_price}</span>
                                      <div style={ordPro}>
                                        {
                                          order.ordered_products.map((product,index)=>{
                                            return(
                                              <div className="d-flex justify-content-center" key={index}>
                                                <Link to={"/product/"+product.productId} style={itemStyle}>{product.name}</Link><span>({product.quantity}) piece</span>
                                              </div>
                                            )
                                          })
                                        }
                                      </div>
                                      <span>{(order.payment).toString()}</span>
                                      <span>
                                        <select onChange={async(e) =>{
                                          const load_load = document.querySelector(".load_load");
                                          load_load.style.animation = "";
                                          load_load.classList.remove("d-none");

                                          const res = await axios.post("/change-deliver",{
                                            orderId:order._id,
                                            value:e.target.value
                                          })
                                          if(res.data === "okay"){
                                            load_load.style.animation = "load_end 0.7s linear";
                                            setRefresh(!refresh);
                                            setTimeout(() => {
                                              load_load.classList.add("d-none");
                                            }, 680);
                                          }
                                        }} value="false" style={{maxWidth:"100px"}} className="form-select mx-auto" aria-label="Default select example">
                                            <option value="false">false</option>
                                            <option value="true">true</option>
                                        </select>
                                      </span>
                                    </div> 
                                  </div>
                                </>
                              )
                            })
                          }
                      </div>
                      <button className="prev control" type="button" data-bs-target="#carouselExampleControlsNoTouching" data-bs-slide="prev">
                        <ChevronLeftIcon className="arrow-icon" style={{background:"#00000091"}}/>
                      </button>
                      <button className="next control" type="button" data-bs-target="#carouselExampleControlsNoTouching" data-bs-slide="next">
                      <ChevronRightIcon className="arrow-icon" style={{background:"#00000091"}}/>
                      </button>
                    </div>
                  </> :
                  <span className="text-secondary">No new order remains</span>
              }

            </> :
            <>
              {/* prev orders */}
              {
                prevOrders.length ?
                  <>
                    <div id="carouselExampleControlsNoTouching" className="carousel slide" data-bs-touch="false" data-bs-interval="false">
                      <div className="carousel-inner">
                          {
                            prevOrders.map((order,ind)=>{
                              return(
                                <>
                                  <div className={ind === 0 ? "carousel-item active" : "carousel-item"} key={ind}>
                                    <div style={{ verticalAlign: "middle" }} className='suc_order rows'>
                                      <b style={{background:"#1e9754"}}>{ind + 1}</b>
                                      <span>{order.customer_name}</span>
                                      <span>{order.customer_email}</span>
                                      <span>{order.country}</span>
                                      <span>{order.city}</span>
                                      <span>{order.area}</span>
                                      <span>{order.customer_phone}</span>
                                      <span>${order.total_price}</span>
                                      <div style={ordPro}>
                                        {
                                          order.ordered_products.map((product,index)=>{
                                            return(
                                              <div className="d-flex justify-content-center" key={index}>
                                                <Link to={"/product/"+product.productId} style={itemStyle}>{product.name}</Link><span>({product.quantity}) piece</span>
                                              </div>
                                            )
                                          })
                                        }
                                      </div>
                                      <span>{(order.payment).toString()}</span>
                                      <span>
                                        <select onChange={async(e) =>{
                                          const load_load = document.querySelector(".load_load");
                                          load_load.style.animation = "";
                                          load_load.classList.remove("d-none");

                                          const res = await axios.post("/change-deliver",{
                                            orderId:order._id,
                                            value:e.target.value
                                          })
                                          if(res.data === "okay"){
                                            load_load.style.animation = "load_end 0.7s linear";
                                            setRefresh(!refresh);
                                            setTimeout(() => {
                                              load_load.classList.add("d-none");
                                            }, 680);
                                          }
                                        }} value="true" style={{maxWidth:"100px"}} className="form-select mx-auto" aria-label="Default select example">
                                            <option value="true">true</option>
                                            <option value="false">false</option>
                                        </select>
                                      </span>
                                    </div> 
                                  </div>
                                </>
                              )
                            })
                          }
                      </div>
                      <button className="prev control" type="button" data-bs-target="#carouselExampleControlsNoTouching" data-bs-slide="prev">
                        <ChevronLeftIcon className="arrow-icon" style={{background:"#00000091"}}/>
                      </button>
                      <button className="next control" type="button" data-bs-target="#carouselExampleControlsNoTouching" data-bs-slide="next">
                      <ChevronRightIcon className="arrow-icon" style={{background:"#00000091"}}/>
                      </button>
                    </div>
                  </> :
                  <span className="text-secondary">There is no completed orders</span>
              }
            </>
        }
      </div>
    </div>
  );
}

export default Table;