import React from 'react';
import StarIcon from "@mui/icons-material/Star";
import StarHalfIcon from "@mui/icons-material/StarHalf";
import StarOutlineIcon from "@mui/icons-material/StarOutline";

function Rating({rating,style}) {
    const fullStar = new Array(Math.floor(rating));
    const blankStar = new Array(5 - Math.round(rating));
  
    let halfStar;
    if (Math.ceil(rating) - rating <= 0.5) {
      halfStar = new Array(1);
      if (Math.ceil(rating) - rating === 0) {
        halfStar = new Array(0);
      }
    } else {
      halfStar = new Array(0);
    }
    for (let i = 0; i < fullStar.length; i++) {
      fullStar[i] = "*";
    }
    for (let i = 0; i < blankStar.length; i++) {
      blankStar[i] = "*";
    }
    for (let i = 0; i < halfStar.length; i++) {
      halfStar[i] = "*";
    }
    return (
        <div className="rating d-flex align-items-center">
          {fullStar.map((_, index) => {
              return <StarIcon key={index} className="star" fontSize="small" style={style}/>;
            })}
            {halfStar.map((_, index) => {
              return (
                <StarHalfIcon key={index} className="star" fontSize="small" style={style}/>
            );
            })}
            {blankStar.map((_, index) => {
              return (
                <StarOutlineIcon
                key={index}
                className="star"
                fontSize="small"
                style={style}
                />
            );
          })}
      </div>
    )
}

export default Rating
