import React from "react";
import { Link } from "react-router-dom";
import Rating from "./Rating";

const styleImg = {
  height: "120px",
  width: "120px",
  objectFit: "contain",
};

function CartSug({
  photo,
  title,
  category,
  categoryLink,
  price,
  link,
  rating,
  onClick,
}) {

  const toTop = () => {
     if(onClick){
       onClick();
     }
  }

  return (
    <div className="cart_sug d-flex" style={{ gap: "10px", maxWidth: "230px" }}>
      <Link onClick={toTop} to={link}>
        <img
          src={photo}
          alt="Category"
          className="d-block"
          style={styleImg}
        />
      </Link>
      <div className="d-flex flex-column align-items-start">
        <Link
           onClick={toTop}
          to={link}
          className="link"
          style={{
            textOverflow: "ellipsis",
            overflow: "hidden",
            WebkitLineClamp: "1",
            display: "-webkit-box",
            WebkitBoxOrient: "vertical",
          }}
        >
          {title}
        </Link>
        <Rating rating={rating} />
        <Link to={categoryLink} className="link">
          <small>{category}</small>
        </Link>
        <span className="d-block" style={{ color: "#B12704" }}>
          ${price}
        </span>
        <Link onClick={toTop} to={link} className="btns2 mt-1">
          <small>Check It</small>
        </Link>
      </div>
    </div>
  );
}

export default CartSug;
