import React from 'react';
import Home from './pages/Home';
import {Switch, Route} from 'react-router-dom';
import Signup from './pages/Signup';
import Login from './pages/Login';
import Category from './pages/Category';
import "./css/Pagination.css"
import Cart from './pages/Cart';
import Product from './pages/Product';
import EmailVerify from './pages/EmailVerify';
import EmailVerifyLog from './pages/EmailVerifyLog';
import Error from './pages/Error';
import Account from './pages/Account';
import UserDetail from './pages/UserDetail';
import ChangeEmail from './pages/ChangeEmail';
import EmailVerifyChange from './pages/EmailVerifyChng';
import ChangeName from './pages/ChangeName';
import ChangePass from './pages/ChangePass';
import Admin from './pages/Admin';
import CreateProduct from './pages/CreateProduct';
import CreateCategory from './pages/CreateCategory';
import CreateReview from './pages/CreateReview';
import PlaceOrder from './pages/PlaceOrder';
import Payment from './pages/Payment';
import MyOrders from './pages/MyOrders';
import TwoStep from './pages/TwoStep';
import Search from './pages/Search'
import ForgetPass from './pages/ForgetPass';
import EmailVerifyForget from './pages/EmailVerifyForget'
import ResetPass from './pages/ResetPass';

function App() {

  return (
    <>
      <Switch>
        <Route exact path='/' component={Home}/>
        <Route exact path='/signup' component={Signup}/>
        <Route exact path='/email-verify' component={EmailVerify}/>
        <Route exact path='/email-verify-loggin' component={EmailVerifyLog}/>
        <Route exact path='/login' component={Login}/>      
        <Route exact path='/category/:category' component={Category}/> 
        <Route exact path='/cart' component={Cart}/>      
        <Route exact path="/product/:productId" component={Product}/>
        <Route exact path="/account" component={Account}/>
        <Route exact path="/user-details" component={UserDetail}/>
        <Route exact path="/change-email" component={ChangeEmail}/>
        <Route exact path="/email-verify-change" component={EmailVerifyChange}/>
        <Route exact path="/change-name" component={ChangeName}/>
        <Route exact path="/change-pass" component={ChangePass}/>
        <Route exact path="/admin" component={Admin}/>
        <Route exact path="/admin/create-product" component={CreateProduct}/>
        <Route exact path="/admin/create-category" component={CreateCategory}/>
        <Route exact path="/create-review/:productId" component={CreateReview}/>
        <Route exact path="/place-order" component={PlaceOrder}/>
        <Route exact path="/payment/:orderId" component={Payment}/>
        <Route exact path="/my-orders" component={MyOrders}/>
        <Route exact path="/two-step-verification/:userId" component={TwoStep}/>
        <Route exact path="/search" component={Search}/>
        <Route exact path="/forget-password" component={ForgetPass}/>
        <Route exact path="/email-verify-forget/:id" component={EmailVerifyForget}/>
        <Route exact path="/reset-password-forget/:id" component={ResetPass}/>
        <Route component={Error}/>
      </Switch>
    </>
  )
}

export default App
